@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
*, *::before, *::after {
  box-sizing: border-box;
}
html,body{
  padding: 0;
  margin: 0;
  background: #000;
  color: #fff;
}

#root {
  width: 640px;
  margin: 0 auto;
}

input {
  line-height: 26px;
  margin-right: 25px;
  font-size: 18px;
  padding-left: 10px;
}

button {
  background: #3085e8;
  border: none;
  padding: 6px 16px;
  border-radius: 2px;
  color: #fff;
  font-size: 14px;
}

.options {
  margin-top: 100px;
}

.cards {
  position: relative;
  height: 400px;
  color: #fff;
  background-color: #000;
}

.card-wrapper {
  position: absolute;
  bottom: 0;
  transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.card {
  position: relative;
  height: 100%;
  width: 15px;
  margin: 0 5px;
  border: 1px solid #ff3179;
  background-color: #000;
  box-shadow: 0 0 25px #c2255c;
}

.card-active {
  -webkit-filter: hue-rotate(200deg);
          filter: hue-rotate(200deg);
}

.card-locked {
  -webkit-filter: hue-rotate(280deg);
          filter: hue-rotate(280deg);
}

.value {
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 1.25rem;
}

.control-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 30px 5px 0;
  padding-top: 20px;
  border-top: 1px solid #fff;
}

h1 {
  margin: 0;
  font-size: 2.5rem;
}
